* {
  font-family: 'Nunito', sans-serif;
}

body {
	background-color: #fafafa;
	background-image: url(./qbkls.png);
}

form {
	background-color: white;
	border-radius: 20px;
}

.logo {
	width: 80px;
}

.banner {
  background-image: url('./lashes-dark.png') !important;
  height: 200px;
  margin: 0 0 30px 0 !important;
  padding: 0 !important;
  color: white;
  font-size: 18px;
}

a:hover {
  color: black;
}

h3 {
  color: black;
}

.btn {
  color: white;
  border-radius: 20px;
}

.btn-primary {
  background-color: #141c8a !important;
  border-color: #2c3e50 !important;
  width: 100%;
}

.btn-primary:hover {
  background-color: #425e7a !important;
  border-color: #425e7a !important;
}

.btn-primary:focus {
  -webkit-box-shadow: 0 0 0 0.2rem rgb(66, 94, 122, 0.25) !important;
          box-shadow: 0 0 0 0.2rem rgb(66, 94, 122, 0.25) !important;
}

.btn-secondary {
  background-color: #61b0b4;
  border-color: #616161;
}

.btn-secondary:hover {
  background-color: #757575;
  border-color: #757575;
}

.btn-secondary:focus {
  -webkit-box-shadow: 0 0 0 0.2rem rgb(117, 117, 117, 0.25) !important;
          box-shadow: 0 0 0 0.2rem rgb(117, 117, 117, 0.25) !important;
}

.footer {
  background-color: #212121;
  color: white;
  font-size: 18px;
  height: 50px;
  padding: 10px 0;
}

.delete-article {
  -webkit-transition: opacity 0.4s ease-in-out;
  -moz-transition: opacity 0.4s ease-in-out;
  -ms-transition: opacity 0.43s ease-in-out;
  -o-transition: opacity 0.4s ease-in-out;
  opacity: 0;
}

@media (min-width: 576px) {
  .banner {
    font-size: calc(10px + 14 * (100vw - 320px) / 800);
  }
  .btn-primary {
    width: auto;
  }
}



